.row {
  margin-right: -15px;
  margin-left: -15px;
  background-color: blue;
  color: blue;
}

.col-xs-12 {
  width: 100%;
  float: left;
}

.alert-danger {
  color: #a94442;
  background-color: #f2dede;
  border-color: #ebccd1;
}

.alert {
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px;
}

.alert-info {
  color: #31708f;
  background-color: #d9edf7;
  border-color: #bce8f1;
}

.alert-link {
    font-weight: bold;
    color: #245269;
    text-decoration: none;
    font-family: Oxygen;
}

.glyphicon {
      position: relative;
      top: 1px;
      display: inline-block;
      font-family: "Glyphicons Halflings";
      font-style: normal;
      font-weight: 400;
      line-height: 1;
}
